
import { mapGetters, mapState } from "vuex";
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

import Scroll from "@/components/scrollAll";
import NoData from "@/components/noData";
import Query from "@/components/filter";
export default {
data() {
return {
clientList: [],
params: {
customerName: "",
beginDate: "",
endDate: "",
page: 1,
count: 10,
saleChiefId: this.$store.state.CurrentUserID
},
pullUp: {
threshold: 0,
txt: {
more: "加载更多",
noMore: "没有更多了"
}
},
pullDown: {
threshold: 50,
stop: 20
},
isSort: false
};
},
methods: {
enter(name) {
this.params.customerName = name;
this.getData(1, 1);
},
selectedUser(id) {
this.params.saleChiefId = id;
this.getData(1, 1);
},
dateQuery(date) {
this.params.beginDate = date.beginDate
? date.beginDate + " 00:00:00"
: "";
this.params.endDate = date.endDate ? date.endDate + " 23:59:59" : "";
this.getData(1, 1);
},
getData(type, page) {
this.$vux.loading.show({
text: "加载中"
});

this.params.page = page || this.params.page;

this.getAxios("/Customer/GetPagedCustomerList", this.params)
.then(res => {
this.$vux.loading.hide();
if (res.Data.List.length > 0) {
if (type === 2) {
this.clientList = [...this.clientList, ...res.Data.List];
this.$refs.scroll.scroll.finishPullUp();
} else {
this.clientList = res.Data.List;
this.$refs.scroll.scroll.finishPullDown();
this.$refs.scroll.scrollTo(0, 0);
this.$refs.scroll.forceUpdate(true);
}
this.params.page += 1;
this.$refs.scroll.refresh();
} else if (type == 1) {
this.clientList = [];
this.$refs.scroll.forceUpdate();
} else {
this.$refs.scroll.forceUpdate();
}
})
.catch(err => {
this.$vux.loading.hide();
console.log(err);
});

},
goto(path, item) {
this.$router.push({
path,
query: item
? {
id: item.ID,
SaleChief: item.SaleChief
}
: {}
});
}
},
mounted() {
this.getData();
this.$bus.on("init", () => {
this.getData(1, 1);
});
const query = this.$route.query;
this.params.beginDate = query.beginDate || "";
this.params.endDate = query.endDate || "";
},
beforeDestroy() {
this.$bus.off("init");
},
components: {
Flexbox,
FlexboxItem,
XHeader,
Scroll,
Group,
XInput,
NoData,
Query
}
};



